import React, { useEffect, useState } from "react";
import NewsItem from "./NewsItem";
import Slider from "./Slider";
import PropTypes from 'prop-types';
// import Footer from "./Footer";
import InfiniteScroll from "react-infinite-scroll-component";

const  News = (props)=> {
  const [articles, setArticles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [totalResults, setTotalResults] = useState(0);
 
 
const capitalizeFirstLetter = (string)=> {
    return string.replace(/^./, string[0].toUpperCase());
}

 const UpdateNews = async () =>{
    props.setProgress(10);
    let url =
    `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=2456e00d09764b37b83ebfcd1bf370e4&page=${page}&pageSize=${props.pageSize}`;
    setLoading(true)
    let data = await fetch(url);
    props.setProgress(30);
    let parsedData = await data.json()
    props.setProgress(70);
    setArticles(parsedData.articles);
    setTotalResults(parsedData.totalResults);
    setLoading(false)
     
       props.setProgress(100);
  }
 useEffect(() => {
   document.title = `${capitalizeFirstLetter( props.category)} - World Times`;
    UpdateNews();
    // eslint-disable-next-line

    
  },[]);


// const  previous= async()=>{

//    setPage(page-1)
//     UpdateNews();
//   }
// const next= async()=>
// {
//         if(!(page + 1 > Math.ceil(totalResults/props.pageSize))){
//           setPage(page+1)
 
//          UpdateNews();
//     } 
//  }
 const fetchMoreData = async () => {
 
let url =
`https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=2456e00d09764b37b83ebfcd1bf370e4&page=${page+1}&pageSize=${props.pageSize}`;
setPage(page+1)
let data = await fetch(url);
let parsedData = await data.json()
setArticles(articles.concat( parsedData.articles))
setTotalResults(parsedData.totalResults)
};
 
    return (
      <>
        <h2 className="text-center heading" >World Times - Top {capitalizeFirstLetter( props.category)} HeadLines   </h2>
        <div className="container " style={{height: "40px", marginBottom:"2px"}}>
        {loading && < Slider/>}
        </div>
        <InfiniteScroll
          dataLength={articles.length}
          next={fetchMoreData}
          hasMore={articles.length !== totalResults}
          loader={<Slider/>}
        >
          <div className="container">
        <div className="row my-3">
          {articles.map((element) => {
            return (
              <div className="col-md-4" key={element.url}>
                <NewsItem
                  title={element.title?element.title.slice(0, 55):""}
                  description={element.description?element.description.slice(0, 95):""}
                  imageURL={element.urlToImage}
                  newsURL={element.url}
                  author = {element.author}
                  date = {element.publishedAt}
                  source ={element.source.name}
                />
              </div>
            );
          })}
        </div>
        </div>
        </InfiniteScroll>
          {/* <Footer/> */}
        </>
    );
  
}
News.defaultProps ={
  country : "us",
  pageSize : 9,
  category : "general"
}
News.propTypes = {
 country : PropTypes.string,
 pageSize : PropTypes.number,
 category : PropTypes.string
}
export default News;
